import {
  Box,
  Flex,
  HStack,
  IconButton,
  Button,
  useDisclosure,
  useColorModeValue,
  Stack,
  useColorMode,
} from "@chakra-ui/react";
import resume from "../Components/Images/Aditi_Sharma_Resume.pdf";
import { HamburgerIcon, CloseIcon, MoonIcon, SunIcon } from "@chakra-ui/icons";
import "../Components/Resume.css";
import { NavLink } from "react-router-dom";
import "./Navbar.css";
import Name from "./Name";
import { SlDocs } from "react-icons/sl";

export default function Navbar() {
  const { colorMode, toggleColorMode } = useColorMode();
  const { isOpen, onOpen, onClose } = useDisclosure();
  // const onButtonClick = () => {
  //   window.open(resume);
  // };

  function downloadFile1() {
    window.open(
      "https://drive.google.com/file/d/1hdlMJToe1tnNuyjfpxhWE6DJXqj7SVic/view?usp=sharing",
      "_blank"
    );
    const link = document.createElement("a");
    link.download = resume;
    document.body.appendChild(link);
    link.click();
  }

  return (
    <div id="nav-menu">
      <Box bg={useColorModeValue("white", "#1a202c")} px={4}>
        <Flex
          h={16}
          alignItems={"center"}
          justifyContent={{ sm: "space-around", lg: "space-around" }}
          // border="1px"
          // borderColor="blue.800"
        >
          <IconButton
            size={"md"}
            icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
            aria-label={"Open Menu"}
            display={{ md: "none" }}
            onClick={isOpen ? onClose : onOpen}
          />
          <HStack
            spacing={40}
            alignItems={"center"}
            fontSize={{ base: "lg", md: "lg", lg: "2xl" }}
            fontWeight={"bold"}
            // border="1px"
            // borderColor="red.800"
          >
            <Box>
              <Name />
            </Box>
            <HStack
              as={"nav"}
              spacing={10}
              display={{ base: "none", md: "flex" }}
              // border="1px"
              // borderColor="blue.800"
              // marginLeft="200px"
            >
              <Button
                className="nav-link home"
                // border="1px"
                // borderColor="blue.800"
                background=""
              >
                <a href="#home">
                  {" "}
                  <b>Home</b>
                </a>
              </Button>

              <Button className="nav-link about" background="">
                <a href="#about">
                  <b>About</b>
                </a>
              </Button>

              <Button className="nav-link skills" background="">
                <a href="#skills">
                  {" "}
                  <b>Skills</b>
                </a>
              </Button>

              <Button className="nav-link projects" background="">
                <a href="#projects">
                  <b>Projects</b>
                </a>
              </Button>

              <Button className="nav-link contact" background="">
                <a href="#contact">
                  <b>Contact</b>
                </a>
              </Button>
            </HStack>
          </HStack>
          <Flex alignItems={"center"} columnGap={2}>
            <Button onClick={toggleColorMode}>
              {colorMode === "light" ? <MoonIcon /> : <SunIcon />}
            </Button>
            <NavLink to="https://drive.google.com/file/d/1hdlMJToe1tnNuyjfpxhWE6DJXqj7SVic/view?usp=sharing">
              <button
                id="resume-button-1"
                className="nav-link resume"
                onClick={downloadFile1}
              >
                <SlDocs />
                RESUME
              </button>
            </NavLink>
          </Flex>
        </Flex>

        {isOpen ? (
          <Box pb={4} display={{ md: "none" }}>
            <Stack as={"nav"} spacing={4}>
              <Button className="nav-link home">
                <a href="#home">
                  {" "}
                  <b>Home</b>
                </a>
              </Button>

              <Button className="nav-link home">
                <a href="#about">
                  <b>About</b>
                </a>
              </Button>

              <Button className="nav-link skills">
                <a href="#skills">
                  {" "}
                  <b>Skills</b>
                </a>
              </Button>

              <Button className="nav-link projects">
                <a href="#projects">
                  <b>Projects</b>
                </a>
              </Button>

              <Button className="nav-link contact">
                <a href="#contact">
                  <b>Contact</b>
                </a>
              </Button>
            </Stack>
          </Box>
        ) : null}
      </Box>
    </div>
  );
}
