import React from "react";
import "./About.css";
import {
  Box,
  Heading,
  Text,
  Stack,
  useBreakpointValue,
} from "@chakra-ui/react";
import { Type } from "./Type";
const About = () => {
  return (
    <div>
      <Stack
        as={Box}
        textAlign={"center"}
        spacing={{ base: 8, md: 14 }}
        py={{ base: 20, md: 20 }}
      >
        <Heading
          fontSize={{ base: "3xl", md: "4xl", lg: "5xl" }}
          lineHeight={2}
        >
          <h1
            as={"span"}
            position={"relative"}
            data-aos={"fade-right"}
            _after={{
              content: "''",
              width: "full",
              height: useBreakpointValue({ base: "20%", md: "30%" }),
              position: "absolute",
              bottom: 1,
              left: 0,
              bg: "red.400",
              zIndex: -1,
            }}
          >
            Hi, I am <span className="about__name">Aditi Sharma</span>
          </h1>
          <Text as={"span"} fontSize={{ lg: "4xl" }} marginBottom={"0px"}>
            <Type />
          </Text>{" "}
        </Heading>
        <Text color={"gray.350"}>
          <Text
            color={"gray.350"}
            fontSize={"20px"}
            fontWeight={"bold"}
            data-aos="fade-right"
            // border="1px"
            // borderColor="green"
            // w={"60%"}
            // marginLeft={"250px"}
            // marginTop={"-10px"}
            // padding={"20px"}
          >
            An innovative Full Stack Web Developer specializing in MERN stack,
            with strong problem- solving and collaboration skills. Thrives in
            fast- paced environments.
          </Text>
        </Text>
        <div className="about__contact center">
          <a
            href="https://github.com/AditiSharma00"
            aria-label="github"
            target="_blank"
            rel="noreferrer"
            className="link"
          >
            <GitHubIcon />
          </a>
          <a
            href="mailto:sharmaaneesh00@gmail.com"
            target="_blank"
            rel="noreferrer"
            aria-label="github"
            className="link"
          >
            <EmailIcon />
          </a>
          <a
            href="tel:+917876044325"
            target="_blank"
            rel="noreferrer"
            aria-label="github"
            className="link"
          >
            <PhoneIcon />
          </a>
          <a
            href="https://www.linkedin.com/in/aditi-sharma-0a607520a/"
            aria-label="github"
            className="link"
            target="_blank"
            rel="noreferrer"
          >
            <LinkedInIcon />
          </a>
          <a
            href="https://drive.google.com/file/d/12t6pRIbzADGCc3Q6tmWBqbwERgEK7UDV/view?usp=sharing"
            aria-label="github"
            className="link"
            target="_blank"
            rel="noreferrer"
          >
            <DescriptionIcon />
          </a>
        </div>
      </Stack>
    </div>
  );
};

export default About;
